






























































import Breadcrumb from '@/components/Breadcrumb/index.vue'
import Hamburger from '@/components/Hamburger/index.vue'
import { Component, Vue } from 'vue-property-decorator'
import { AppModule } from '@/store/modules/app'
import { UserModule } from '@/store/modules/user'

const AV = require('leancloud-storage')

@Component({
  components: {
    Breadcrumb,
    Hamburger
  }
})
export default class Navbar extends Vue {
  private usricon:string = ''

  get sidebar() {
    return AppModule.sidebar
  }

  get avatar() {
    if (this.usricon === undefined) {
      return UserModule.avatar
    }

    return this.usricon
  }

  get usrname() {
    return UserModule.name
  }

  get roles() {
    return UserModule.roles
  }

  private toggleSideBar() {
    AppModule.ToggleSideBar(false)
  }

  created() {
    this.fetchUsrIcon()
  }

  private fetchUsrIcon() {
    var currentUser = AV.User.current()
    var username = currentUser.attributes['username']
    var cql = 'select * from UsrInfo where username = ?'
    var pvalue = [username]
    AV.Query.doCloudQuery(cql, pvalue).then((results:any) => {
      results.results.forEach((ele:any) => {
        const attr = ele.attributes
        this.usricon = attr['usericon']
      })
    }, (error:any) => {
      console.log(error)
    })
  }

  private logout() {
    UserModule.LogOut().then(() => {
      AV.User.logOut()
      location.reload()
    })
  }
}
