import request from '@/utils/request'

export const login:any = (username: string, password: string) =>
  request({
    url: '/1.1/login', // '/user/login',
    method: 'post',
    data: {
      username,
      password
    }
  })

export const getUserInfo = (token: string) =>
  request({
    url: '/user/info',
    method: 'get',
    params: { token }
  })

export const logout = () =>
  request({
    url: '/user/logout',
    method: 'post'
  })
